<template>	
	<div>
		<div style="">
			<div class="left">
				<h2>{{message}}</h2>
				<i class="icon iconfont icon-gouwucheman"></i>
				<button @click="add">添加</button>
				<table>
				 	<th>
				 		<td>姓名</td>
				 		<td>年龄</td>
				 	</th>
				 	<tr v-for="item in lefts">
				 		<td>{{item.name}}</td>
				 		<td>{{item.age}}</td>
				 	</tr>
				 </table>
		<input type="text" v-model="name" />
		<button @click="query">查询</button>
			</div>
			<div class="right">
				<situation :data="rights"></situation>
				 
			</div>
		</div>
		
	</div>
</template>

<script>
	import situation from "./situation"
	
	export default {
		name:"home",
		data(){
			return {
				message:"我是首页",
				lefts:[],
				rights:[],
				name:""
			}
		},
		methods:{
			query(){
				this.rights=this.lefts.filter(r=>r.name==this.name);
			},
			add(){
				this.lefts.push({name:"张三"+Date.now(),age:12});
			}
		},
		components:{situation}
	}
</script>

<style>
	.left{
		float:left;
		width:50%;
		height:400px;
	}
	.right{
		float: right;
			height:400px;
		width: 50%;
	}
</style>


