<template>
<div>
	<div >
		<el-row>
			<el-col :span="9" class="pos" id="pos-order">
				<el-tabs type="border-card">
					  <el-tab-pane label="点菜">
					  	<el-table :data="goodsList" border >
					  		<el-table-column prop="goodsName" align="center" label="商品名称" width="100">
					  			
					  		</el-table-column>
					  		<el-table-column prop="count" align="center" label="数量" width="80">
					  			
					  		</el-table-column>
					  		<el-table-column prop="price" align="center" label="价格" width="80">
					  			
					  		</el-table-column>
					  		<el-table-column  align="center" label="操作" fixed="right" width="150">
					  			<template slot-scope="scope">
					  				<el-button  size="mini">增加</el-button>
					  				<el-button type="danger" size="mini">删除</el-button>
					  			</template>
					  		</el-table-column>
					  	</el-table>
					  	<div class="operate"> 
					  		<el-button type="warning">挂单</el-button>
					  		<el-button type="danger">删除</el-button>
					  		<el-button type="success">结账</el-button>
					  	</div>
					  </el-tab-pane>
	  				<el-tab-pane label="挂单">挂单</el-tab-pane>
					  <el-tab-pane label="外卖">外卖</el-tab-pane>
				</el-tabs>
			</el-col>
			<el-col :span='15'>
				我是产品
			</el-col>
		</el-row>
	</div>
</div>
  </template>

<script>
export default {
  name: 'HelloWorld',
  mounted(){
  	var clientHeight=document.body.clientHeight;
  	document.getElementById("pos-order").style.height=clientHeight+"px";
  },
  data(){
  	return {
  		goodsList:[{
  			goodsName:"香辣鸡腿堡",
  			count:0,
  			price:50
  		},
  		{
  			goodsName:"嫩牛五方",
  			count:0,
  			price:22
  		},
  		{
  			goodsName:"薯条",
  			count:0,
  			price:12
  		},
  		{
  			goodsName:"可乐",
  			count:0,
  			price:8
  		}]
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.pos{
	background-color: #f9fafc;
	border-right: 1px solid #c0ccda;
}
.operate{
	margin-top: 15px;
}
</style>



